import React, { Component } from 'react';
import { Input, DatePicker, Select, Button } from 'antd';
import moment from 'moment';
import styles from './index.module.less';

const { RangePicker } = DatePicker;

export default class SearchBox extends Component {
    constructor(props){
        super(props);
        this.state = {
            statusList:[
                {label:"全部",value:"0",color:""},
                {label:"计划审核中",value:"2",color:"#FADB14"},
                {label:"计划表单填报中",value:"4",color:"#3F54A2"},
                {label:"计划审核退回",value:"3",color:"#FA541C"},
                {label:"计划超时",value:"5",color:"#FA8C16"},
                {label:"完成",value:"6",color:"#52C41A"},
                {label:"编辑中",value:"1",color:"#52C41A"},
            ],
            name:"",
            // rangeTime:["2019-08-21", "2019-09-17"],
            rangeTime:[],
            department:"",
            status:"0",
            isAdmainistrator:false,
        }
    }
    componentDidMount(){
        this.search();
        try {//拥有管理员角色判断
            let userInfo = JSON.parse(window.localStorage.getItem("leftMenuList"));
            let isAdmainistrator = userInfo.userRole.some(item=>item.roleId==="079d6f4d2fe215cf2ede1d481b7ed8c1");
            this.setState({
                isAdmainistrator
            })
        } catch (error) {
            console.log(error)
        }
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.search();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    _changeName = (e) => {
        this.setState({name:e.target.value})
    }
    _changeTime = (time,timeString) => {
      
        this.setState({rangeTime:time})
    }
    _changeDepart = (e) => {
        this.setState({department:e.target.value})
    }
    _changeStatus = (val) => {
        this.setState({status:val})
    }
    search = () => {
        let params = {
            planTaskName:this.state.name,
            taskStartTime:this.state.rangeTime[0]?moment(this.state.rangeTime[0]).format('YYYY-MM-DD HH:mm:ss'):"",
            taskEndTime:this.state.rangeTime[1]?moment(this.state.rangeTime[1]).format('YYYY-MM-DD HH:mm:ss'):"",
            creatorName:this.state.department,
            taskStatus:this.state.status || null,
        }
        if(params.taskStatus === '0'){
            params.taskStatus=null
        }
        this.props.search(params);
    }
    handleReset=()=>{
        this.setState({
            name:"",
            rangeTime:[],
            department:"",
            status:"0",
        })
        let params = {
            planTaskName:'',
            taskStartTime:"",
            taskEndTime:"",
            creatorName:'',
            taskStatus:null,
        }
        if(params.taskStatus === '0'){
            params.taskStatus=null
        }
        this.props.search(params);
    }
    render() {
        return (
            <div className={styles.filterBox}>
                <div className={styles.filterItem}>
                    计划名称：
                    <Input style={{width:220}} placeholder="请输入计划名称" value={this.state.name} onChange={this._changeName}/>
                </div>
                <div className={styles.filterItem}>
                    发起时间：
                    <RangePicker style={{width:330}} value={this.state.rangeTime} onChange={this._changeTime} />
                </div>
                {
                    this.state.isAdmainistrator?
                    <div className={styles.filterItem}>
                        发起人/部门：
                        <Input style={{width:220}} placeholder="请输入关键字查询" value={this.state.department} onChange={this._changeDepart}/>
                    </div>:null
                }
                {/* <div className={styles.filterItem}>
                    发起人/部门：
                    <Input style={{width:220}} placeholder="请输入关键字查询" onChange={this._changeDepart}/>
                </div> */}
                <div className={styles.filterItem}>
                    计划状态：
                    <Select style={{width:140}} onChange={this._changeStatus} value={this.state.status}>
                        {this.state.statusList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.filterItem}>
                    <Button type='primary' ghost icon="search" onClick={this.search}>搜索</Button>   

                </div>
                <div  className={styles.filterItem}>
                    <Button icon="sync" onClick={this.handleReset}>重置</Button>
                </div>
            </div>
        )
    }
}
