import React, { Component } from 'react';
import { Table, Steps, Divider, Popover, Timeline, message } from 'antd';
import moment from 'moment';
import { getAction } from '@/axios';

// const statusList = [//状态备注
//     {label:"全部",value:0},
//     {label:"完成",value:4},
//     {label:"填报中",value:1},
//     {label:"填报审核中",value:2},
//     {label:"审核退回",value:3},
//     {label:"填报超时",value:5}
// ]
const auditStatusList = [
    {color:"#52C41A",text:"通过"},
    {color:"red",text:"退回"},
    {color:"#52C41A",text:"审核中"},
    {color:"orange",text:"待审批"}
]
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}
export default class FillInReview extends Component {
    constructor(props){
        super(props);
        this.state = {
            expandedRowKeys:[],//一级展开行数据
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:100,
                },{
                    title:"工号",
                    dataIndex:"userCode",
                    width:140
                },{
                    title:"填报人",
                    dataIndex:"userName",
                    width:140,
                    render:text => <span>{text}</span>
                },{
                    title:"所属单位",
                    dataIndex:"departName",
                    width:200
                },
                // {
                //     title:"职务",
                //     dataIndex:"positionName",
                //     width:160
                // },
                {
                    title:"状态",
                    dataIndex:"status",
                    width:120,
                    render:(text)=>{
                        return text !== "未完成"?<span style={{color:"#52C41A"}}>{text}</span>:<span style={{color:"#FA541C"}}>{text}</span>
                    }
                },{
                    title:"操作",
                    width:100,
                    render:(text,record) => {
                        return this.state.expandedRowKeys.length>0 && this.state.current.userId === record.userId ?<span 
                            onClick={() => {this.onExpand(false,record)}}
                            style={{color:"#24695C",cursor:"pointer"}}
                            >
                                收起详情
                            </span>:<span 
                            onClick={() => {this.onExpand(true,record)}}
                            style={{color:"#24695C",cursor:"pointer"}}
                            >
                                展开详情
                            </span>
                        }
                }
            ],
            dataSource:[
                // {departName:"信息管理部",userName:"张三",userCode:"55114",positionName:"教学督导",status:1,formList:[
                //     {formName:"教师信息收集表",status:1,formId:1},
                //     {formName:"教师信息收集表",status:2,formId:2}
                // ]},
            ],
            expandTableRowKeys:[],//二级table咱开行数据
            stepsData:{current:0,setps:[]},
            pageInfo:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total)=>(`共${total}条`)
            },
            current:null
        }
    }
    componentDidMount(){
        this.setState({
            planTaskId:this.props.planTaskId || "",
            processId:this.props.processId || ""
        },()=>{
            this.getTableData();
        })
    }
    //获取一级和二级table数据
    getTableData = () => {
        const planTaskId=this.state.planTaskId;
        const pageNum=this.state.pageInfo.current;
        const pageSize=this.state.pageInfo.pageSize;
        getAction(`/ybt/planTask/queryPlanUserFormList?planTaskId=${planTaskId}&pageNum=${pageNum}&pageSize=${pageSize}`).then(res => {
            if(res.success){
                this.setState({
                    dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                    pageInfo:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal:(total)=>(`共${total}条`)
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //分页
    _handleTableChange = (pageInfo) => {
        this.setState({
            pageInfo:{
                ...this.state.pageInfo,
                current:pageInfo.current
            }
        },this.getTableData)
    }

    //点击展开一级图标或者查看详情
    onExpand = (expanded, record) => {
        this.setState({
            expandedRowKeys:expanded?[record.sortIndex]:[],
            current:record
        })
    }
    //展开二级table
    expandTableRender = (record, index) => {
        const onExpandTable = (expanded,record) => {
            this.setState({
                expandTableRowKeys:expanded?[record.sortIndex]:[],
            });
            this.getStepDataRequest(record.formTaskId,record.processId);
        }
        const columns = [
            {
                title:"表单序号",
                dataIndex:"sortIndex"
            },{
                title:"表单名称",
                dataIndex:"formName"
            },{
                title:"是否填报",
                dataIndex:"status",
                render:(text)=>{
                    let isDone = text===2 || text === 4 || text === 5;
                    return isDone?<span style={{color:"#52C41A"}}>已填报</span>:<span style={{color:"#FA541C"}}>未填报</span>
                }
            },{
                title:"操作",
                render:(text,record) => (<>
                    <span 
                        onClick={() => {onExpandTable(true,record)}}
                        style={{color:"#24695C",cursor:"pointer"}}
                    >
                        查看进度
                    </span>
                    <Divider type="vertical"/>
                    <span
                        onClick={() => {this._exportResult(record)}}
                        style={{color:"#24695C",cursor:"pointer"}}
                    >
                        导出结果
                    </span>
                </>)
            }
        ];
        // const dataSource = [
        //     { formId: "a", formName: "测试1", userId: "1", status: 1 },
        // ]
        
        const dataSource = record.formList.map((item,sortIndex) => ({
           ...item,
           sortIndex:`${index+1}.${sortIndex+1}`
        }))
        return (
            <Table
                rowKey="sortIndex"
                columns={columns}
                dataSource={dataSource}
                expandedRowRender={this.expandStepsRender}
                onExpand={onExpandTable}
                expandedRowKeys={this.state.expandTableRowKeys}
                pagination={false}
            />
        )
    }
    //导出结果
    _exportResult = (record) => {
        // message.info("导出结果",record);
        window.open(`http://${window.location.host}/ybt/form/dl/export?formTaskId=${record.formTaskId}&formId=${record.formId}`)
    }
    //展开的步骤条
    expandStepsRender = (record, index, indent, expanded) => {
        // console.log(record)
        return (
            <Steps current={Number(this.state.stepsData.current)} progressDot={true}>
                {
                    this.state.stepsData.setps.map((item,index) => (
                        <Steps.Step key={index} title={item.title} description={
                            <Popover
                                content={
                                    <Timeline>
                                        {
                                            item.toolTips?item.toolTips.map((el,index) => (
                                                <Timeline.Item key={index}>
                                                    <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                        {el.job}
                                                        <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                        {
                                                            findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:"状态未知"
                                                        }
                                                    </p>
                                                    {
                                                        (el.status==='通过' || el.status==='退回')?
                                                        <>
                                                            <p style={{color:"rgba(0,0,0,0.65)"}}>审批意见：<span>{el.comments}</span></p>
                                                            <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                        </>:null
                                                    }
                                                </Timeline.Item>
                                            )):null
                                        }
                                    </Timeline>
                                }
                            >
                                <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'(或签)':'(会签)':''}</span>
                            </Popover>
                        } />
                    ))
                }
            </Steps>
        )
    }
    getStepDataRequest = (formTaskId,processId) => {
        getAction(`/ybt/planTask/queryFormProcess?formTaskId=${formTaskId}&processId=${processId}`).then(res => {
            if(res.success){
                this.setState({stepsData:res.obj})
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <Table
                rowKey="sortIndex"
                columns={this.state.columns}
                dataSource={this.state.dataSource}
                expandedRowRender={this.expandTableRender}
                // indentSize={5}
                onExpand={this.onExpand}
                expandedRowKeys={this.state.expandedRowKeys}
                pagination={this.state.pageInfo}
                onChange={this._handleTableChange}
            />
        )
    }
}
