import React, { Component } from 'react';
import { Tabs, Row, Col } from 'antd';
import moment from 'moment';
import FillInSituation from './fillInSituation';
import PlanningReview from './planningReview';
import FillInReview from './fillInReview';
import styles from '../index.module.less';

const statusList = [
    {label:"全部",value:0,color:""},
    {label:"计划审核中",value:2,color:"#FADB14"},
    {label:"计划表单填报中",value:4,color:"#3F54A2"},
    {label:"计划审核退回",value:3,color:"#FA541C"},
    {label:"计划超时",value:5,color:"#FA8C16"},
    {label:"完成",value:6,color:"#52C41A"},
    {label:"编辑中",value:1,color:"#52C41A"},
];

export default class Detail extends Component {
    constructor(props){
        super(props);
        this.state = {
            detailInfo:{
                // planTaskId:"1",//计划id
                // planTaskName:"填报计划测试1",//计划名称
                // planAttachmentNum:1,//附件数量
                // planFillNum:5,//填报数量
                // taskStartTime:1234567,//发起时间
                // taskEndTime:1234567,//截至时间
                // creatorName:"管理员",//发起人
                // creatorDepartName:"测试部门一",//发起部门
                // taskStatus:1,//计划状态
                // processId:1,//流程id
                // deliverType:0,任务类型
            },
            showExport:false
        }
    }
    componentWillMount(){
        let showExport = false;
        if(this.props.currentPlanInfo.taskStatus>=4){
            showExport = true;
        }
        this.setState({
            detailInfo:{...this.props.currentPlanInfo},
            showExport
        })
    }
    render() {
        const startTime = this.state.detailInfo.taskPlanStartTime?moment.unix(this.state.detailInfo.taskPlanStartTime/1000).format("YYYY/MM/DD HH:mm:ss"):"";
        const endTime = this.state.detailInfo.taskPlanEndTime?moment.unix(this.state.detailInfo.taskPlanEndTime/1000).format("YYYY/MM/DD HH:mm:ss"):"";
        return (
            <React.Fragment>
                <div className={styles.detailInfo}>
                    <Row style={{marginBottom:20}}>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}>计划名称：</span>
                            {this.state.detailInfo.planTaskName}
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}>填报时间：</span>
                            {`${startTime}-${endTime}`}
                        </Col>
                        <Col span={6}>
                            <span style={{display:"inline-block",width:100}}>状态：</span>
                            {statusList.find(item => item.value === this.state.detailInfo.taskStatus)?
                                statusList.find(item => item.value === this.state.detailInfo.taskStatus).label:"暂无状态"
                            }
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}>发起人：</span>
                            {this.state.detailInfo.creatorName}
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}>发起部门：</span>
                            {this.state.detailInfo.creatorDepartName}
                        </Col>
                        <Col span={6}>
                            <span style={{display:"inline-block",width:100}}>任务类型：</span>
                            {this.state.detailInfo.deliverType===0?'所有填报人填同样表':'按表单配置填报人'}
                        </Col>
                    </Row>
                </div>
                <Tabs onChange={this.changeTab} type="card">
                    <Tabs.TabPane tab="填报情况" key="1">
                        <FillInSituation showExport={this.state.showExport} planTaskId={this.state.detailInfo.planTaskId}/>
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="计划审核情况" key="2">
                        <PlanningReview planTaskId={this.state.detailInfo.planTaskId} processId={this.state.detailInfo.processId}/>
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="填报审核情况" key="3">
                        <FillInReview planTaskId={this.state.detailInfo.planTaskId} processId={this.state.detailInfo.processId}/>
                    </Tabs.TabPane>
                </Tabs>
            </React.Fragment>
        )
    }
}
