import React, { Component } from 'react';
import { Table, Select, Input, Button, Row, Col, message } from 'antd';
import styles from '../index.module.less';
import { getAction, postAction } from '@/axios';

const statusList = [
    {label:"全部",value:0,color:""},
    {label:"完成",value:4,color:"#52C41A"},
    {label:"填报中",value:1,color:"#52C41A"},
    {label:"填报审核中",value:2,color:"#FADB14"},
    {label:"审核退回",value:3,color:"#FA541C"},
    {label:"填报超时",value:5,color:"#FA8C16"},
];

export default class FillInSituation extends Component {
    constructor(props){
        super(props);
        this.state = {
            fillPaneLeftColumns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:60
                },{
                    title:"表格名称",
                    dataIndex:"formName",
                    width:110,
                    render:(text) => (<span title={text}>{text}</span>)
                },{
                    title:"已完成人数",
                    dataIndex:"overNum",
                    width:110
                },{
                    title:"应填报人数",
                    dataIndex:"totalNum",
                    width:110
                },{
                    title:"完成比例",
                    dataIndex:"percent",
                    width:100,
                    render:(text)=>`${text}%`
                },{
                    title:"操作",
                    width:100,
                    render:(text,record) => (
                        this.props.showExport?<span 
                            style={{textDecoration:"underLine",color:"#24695C",cursor:"pointer"}} 
                            onClick={(e)=>{this.exportResult(record,e)}}
                        >
                            导出结果
                        </span>:null
                    )
                }
            ],
            fillPaneLeftDataSource:[
                // {
                //     sortIndex:1,
                //     formName:"表单1",
                //     overNum:"90",
                //     totalNum:"100",
                //     percent:90,
                //     formId:"1"
                // },
            ],
            fillPaneRightColumns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:80
                },{
                    title:"填报人",
                    dataIndex:"userName",
                    width:100
                },{
                    title:"所属单位",
                    dataIndex:"departName",
                    width:120
                },
                // {
                //     title:"职务",
                //     dataIndex:"position_name",
                //     width:100
                // },
                {
                    title:"状态",
                    dataIndex:"taskStatus",
                    width:100,
                    render:(text)=>{
                        const currentStatus = statusList.find(item => item.value===text);
                        if(currentStatus){
                            return (<><i style={{display:"inline-block",width:8,height:8,borderRadius:"50%",marginRight:3,background:currentStatus.color}}/>{currentStatus.label}</>)
                        }else{
                            return "状态未统计"
                        }
                    }
                }
            ],
            fillPaneRightDataSource:[
                // {
                //     sortIndex:"1",
                //     userName:"丁平",
                //     departName:"信息管理部",
                //     position_name:"主任",
                //     taskStatus:1
                // },
            ],
            currentFillPaneRecord:{},//当前左侧选择的表单信
            planTaskId:"",//计划id
            userName:"",//表单筛选条件-名称
            taskStatus:0,//表单筛选条件-状态
            leftPageInfo:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total)=>`共${total}条`
            },
            rightPageInfo:{
                current:1,
                pageSize:10,//测试要求改10
                total:0,
                showTotal:(total)=>`共${total}条`
            }
        }
    }
    componentWillMount(){
        this.setState({
            planTaskId:this.props.planTaskId
        },()=>{
            this.getLeftPlanInfoListRequest()
        })
    }
    //获取左侧计划信息列表---请求
    getLeftPlanInfoListRequest = () => {
        getAction(`/ybt/planTask/queryPlanInfoList?planTaskId=${this.state.planTaskId}&pageNum=${this.state.leftPageInfo.current}&pageSize=${this.state.leftPageInfo.pageSize}`).then(res => {
            if(res.success){
                this.setState({
                    fillPaneLeftDataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item})),
                    leftPageInfo:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal:(total)=>`共${total}条`
                    },
                    currentFillPaneRecord:res.obj.rows?res.obj.rows.map((item,index)=>({sortIndex:index+1,...item}))[0]:{}
                },this.getFillDetailRequest)
            }else{
                message.error(res.obj)
            }
        })
    }
    //左侧翻页
    _changeLeftPageInfo = (pageInfo) => {
        this.setState({
            leftPageInfo:{
                current:pageInfo.current,
                pageSize:pageInfo.pageSize,
                total:pageInfo.total,
                showTotal:(total)=>`共${total}条`
            }
        },()=>{
            this.getLeftPlanInfoListRequest();
        })
    }
    //左侧行点击
    onRow = (record) => {
        return {
            onClick:() => {
                this.setState({
                    currentFillPaneRecord:record,
                    userName:"",
                    taskStatus:0,
                },this.getFillDetailRequest)
            }
        }
    }
    //导出结果
    exportResult = (record,e) => {
        e.stopPropagation();
        // console.log("当前到处数据：",record);
        if(record.overNum<=0){
           message.warning("填报人数为0，不支持导出！");
           return false; 
        }
        window.open(`http://${window.location.host}/ybt/form/dl/result/list/downLoad?planTaskId=${record.planTaskId}&formId=${record.formId}`)
    }
    _changeUserName = (e) => {
        this.setState({userName:e.target.value})
    }
    _changeTaskStatus = (val) => {
        this.setState({taskStatus:val})
    }
    _search = () => {
        this.getFillDetailRequest();
    }
    //右侧表单填写详情--请求
    getFillDetailRequest = () => {
        if(!this.state.currentFillPaneRecord.formId){
            return false
        }
        let params = {
            formId:this.state.currentFillPaneRecord.formId,
            userName:this.state.userName,
            taskStatus:this.state.taskStatus,
            planTaskId:this.props.planTaskId
        }
        if(params.taskStatus === 0){
            delete params.taskStatus
        }
        postAction(`/ybt/planTask/queryFormInfoList?pageNum=${this.state.rightPageInfo.current}&pageSize=${this.state.rightPageInfo.pageSize}`,params).then(res => {
            if(res.success){
                this.setState({
                    rightPageInfo:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal:(total)=>`共${total}条`
                    },
                    fillPaneRightDataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //右侧翻页
    _changeRightPageInfo = (pageInfo) => {
        this.setState({
            rightPageInfo:{
                current:pageInfo.current,
                pageSize:pageInfo.pageSize,
                total:pageInfo.total,
                showTotal:(total)=>`共${total}条`
            }
        },()=>{
            this.getFillDetailRequest();
        })
    }
    render() {
        return (
            <Row style={{marginTop:"-16px"}}>
                <Col span={12}>
                    <Table
                        rowKey="sortIndex"
                        columns={this.state.fillPaneLeftColumns}
                        dataSource={this.state.fillPaneLeftDataSource}
                        onRow={this.onRow}
                        rowClassName={(record) => record.formId===this.state.currentFillPaneRecord.formId?styles.selectRow:""}
                        pagination={this.state.leftPageInfo}
                        onChange={this._changeLeftPageInfo}
                    />
                </Col>
                <Col span={12} style={{paddingLeft:"20px"}}>
                    <h5 style={{color:"#19233C",fontWeight:"bold",fontSize:16,paddingTop:9}}>{this.state.currentFillPaneRecord.formName} 填报情况</h5>
                    <div style={{color:"#232425",marginBottom:10}}>
                        填报人：
                        <Input style={{width:140,marginRight:8}} placeholder="姓名/工号" value={this.state.userName} onChange={this._changeUserName}/>
                        填报状态：
                        <Select style={{width:96,marginRight:8}} value={this.state.taskStatus} onChange={this._changeTaskStatus}>
                            {statusList.map(item => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                        </Select>
                        <Button icon="search" onClick={this._search}>搜索</Button>
                    </div>
                    <Table
                        rowKey="sortIndex"
                        columns={this.state.fillPaneRightColumns}
                        dataSource={this.state.fillPaneRightDataSource}
                        pagination={this.state.rightPageInfo}
                        onChange={this._changeRightPageInfo}
                    />
                </Col>
            </Row>
        )
    }
}
