import React, { useState, useEffect } from 'react';
import { Table, message } from 'antd';
import { postAction } from '@/axios';

export default function AttachmentTable(props){
    const columns = [
        {
            title:"序号",
            dataIndex:"sortIndex",
            width:"20%"
        },{
            title:"附件名称",
            dataIndex:"planAttachmentName",
            width:"40%",
            render:text => (<span title={text}>{text}</span>)
        },{
            title:"附件格式",
            dataIndex:"planAttachmentType",
            width:"20%"
        },{
            title:'操作',
            width:'80px',
            render:(text,record) => {
                return <span className='operation-link' onClick={()=>{downLoad(record)}}>下载</span>
            }
        }
    ];
    const [ dataSource, setDataource ] = useState([]);
    
    let getAttachmentRequest = (planTaskId) => {
        postAction("/ybt/planTask/getPlanAttachments",{planTaskId}).then(res => {
            if(res.success){
                setDataource(res.obj.map((item,index)=>({sortIndex:index+1,...item})))
            }else{
                message.error(res.obj)
            }
        })
    }

    let downLoad = (record) => {
        window.open(`http://${window.location.host}/ybt/planTask/download/planAttachment?planAttachmentId=${record.planAttachmentId}`)
    }

    useEffect(() => {
        getAttachmentRequest(props.planTaskId)
      },[props.planTaskId]
    );
    return (
        <Table
            rowKey="sortIndex"
            dataSource={dataSource}
            columns={columns}
        />
    )
}
